/* PAGE CONTROLS ===========================================*/

.navigation-mininav-wrapper .container {
  background-color: var(--primary-color);
  border-radius: 40px;
  -moz-box-shadow: 10px 10px 20px -5px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 10px 10px 20px -5px rgba(0, 0, 0, 0.25);
  box-shadow: 10px 10px 14px -10px rgba(0, 0, 0, 0.25);
  height: 40px;

  padding-top: 2px;
  width: 188px;


  .controls * {
    box-sizing: border-box;
    margin: 0;
  }

  .controls {
    align-content: center;
    color: var(--compact-nav-color);
    display: grid;
    font-family: 'Lato', sans-serif;
    grid-template-columns: 40px 1fr 40px;
    justify-content: center;
    margin: 0 auto;
    position: relative;
    transition: all 0.3s ease;
    width: 100%;
  }

  .controls .left,
  .controls .right {
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    height: 37px;
    overflow: hidden;
    position: relative;
    width: 33px;

    i {
      position: absolute;
      width: 33px;
      height: 33px;
    }
  }

  .controls .left {
    transform: rotate(180deg);
    left: 9px;
  }

  .controls .right {
    margin-left: -2px;
    margin-top: -1px;

  }

  .controls i {
    position: relative;
    top: 5px;
    border-radius: 35px;
  }

  .controls .left i {
    top: 6px;
  }

  .controls .directional-btn {
    cursor: pointer;
    font-size: 16px;
    color: var(--compact-nav-color);
  }

  .controls .directional-btn {
    &:hover {
      .chevron-color {
        fill: rgba(0, 0, 0, 0.4);
      }

    }
  }

  .controls .page {
    border: 3px solid var(--compact-nav-spacer);
    font-size: 18px;
    height: 35px;
    line-height: 34px;
    margin: 0 5px 0 0;
    min-width: 120px;
    overflow: hidden;
    text-align: center;
    width: 100%;
    border-bottom: 0;
    border-top: 0;
  }
}
