/* mini nav stepper dots */
.navigation-mininav-wrapper {
  align-items: center;
  display: grid;
  justify-items: center;
  min-height: 60px;
  padding: 0 2em;
  width: 100%;
  margin-top: -20px;

}
.card-container__dots-wrapper {
  display: grid;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-template-areas: "arrowLeft dots arrowRight";
  grid-template-columns: 30px auto 30px;
  height: auto;
  justify-content: center;
  list-style-type: none;
  margin: 0;
  min-height: 30px;
  padding: 0;
  z-index: 999;
}

.card-container__dot {
  align-self: center;
  background-color: var(--primary-color-inactive, rgb(192, 226, 255));
  border-radius: 50%;
  color: white;
  cursor: pointer;
  height: 12px;
  justify-self: center;
  margin: 3px 3px;
  width: 12px;

  &.active {
    background-color: var(--primary-color, rgb(46, 159, 255));
    border: 4px double;
    height: 1.2em;
    transition: 0.5s;
    width: 1.2em;
    align-self: center;
  }
  &:focus,
  &:hover {
    background-color: var(--primary-color, rgb(46, 159, 255));
    transition: 0.5s;
  }
  &:focus {
    content: '';
    animation: pulse 1.5s ease 0.2s infinite;
    outline: none;
  }
  &:focus-visible {
    outline: 3px solid var(--primary-color-hover); /* That'll show 'em */
  }
}

.dots-container__wrapper {
  align-content: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 30px;
  justify-content: center;
  margin: 0;
  padding: 0;
}

/* mini-nav stepper end arrows */
.control {
  height: 24px;
  width: 24px;
  border-radius: 50%;

  &:focus,
  &:hover {
    background-color: var(--primary-color, rgb(46, 159, 255));
    transition: 0.5s;
  }
  &:focus {
    content: '';
    animation: pulse 1.5s ease 0.2s infinite;
    outline: none;
  }
  &:focus-visible {
    outline: 3px solid var(--primary-color-hover); /* That'll show 'em */
  }

  &.left {
    grid-area: arrowLeft;
    justify-self: start;
  }
  
  &.right {
    grid-area: arrowRight;
    justify-self: end;
  }
}



/* svg chevron */
.chevron {
  height: 24px;
  width: 24px;
  position: relative;
  &.right {
    svg.chevron {
      top: 2px;
      left: 2px;
    }
  }
  &.left {
    svg.chevron {
      top: 2px;
      left: 2px;
      transform: rotate(180deg);
    }
  }
}
svg.chevron {
  height: 84%;
  width: 84%;
  path.arrow {
    fill: var(--chevron-color);
  }
}

