@import './variables';
@import './animation';

*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  min-height: 100vh;
}
body {
  margin: 0;
  font-family: var(--body-font, 'Open Sans', 'sans-serif');
  font-size: 1rem;
}
input,
button {
  padding: 0;
  border: 0;
  background: none;
  font-family: var(--body-font, 'Open Sans', 'sans-serif');
  outline: none;
  font-size: 1.5em;
}
button {
  cursor: pointer;
}
svg {
  display: block;
  width: 100%;
  height: 100%;
}
.scene {
  width: 100%;
  height: 100%;
}
.error {
  text-align: center;
  color: #d93d32;
}
.off-screen {
  position: absolute;
  left: -99999px;
}

#root {
  height: 100vh;
  width: 100vw;
  position: fixed;

  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(28, 28, 28, 0.5);
}
.wrapper {
  width: 100%;
}
ol {
  list-style-type: none;
}
.flashcard__instructions, .hidden {
  border:0;
  clip: rect(0,0,0,0);
  height: 1px;
  margin:-1px;
  overflow:hidden;
  padding:0;
  position:absolute;
  width: 1px;
}

.skip-link {
  background: var(--primary-color, rgba(0,0,0, 0.7));
  color: #fff;
  font-weight: 700;
  left: 50%;
  padding: 4px;
  position: absolute;
  transform: translateY(-100%);
  transition: transform 0.3s;
}

.skip-link:focus {
  transform: translateY(0%);
}
.skip-link:focus-within {
  transform: translateY(0%);
}
@import './cards';
@import './mini-nav';
@import './mini-nav-compact.scss';

