:root {
  --card-radius: 1vw;
  --chevron-color: #fff;
  --dot-color: #ccc;
  --dot-color-hover: rgba(46, 159, 255, 1);
  --auto-grid-min-size: 340px;
  --fluid-scale-font-size: calc(0.875rem + 0.5 * ((100vw - 20rem) / 60));
  --body-font: 'Open Sans', 'sans-serif';
  --title-font: 'Open Sans', 'sans-serif';
  --primary-color:  rgba(46, 159, 255, 1);
  --primary-color-pulse-start:rgba(151, 207, 255, 1);
  --primary-color-pulse: hsla(185, 100%, 70%, 0.007);
  --primary-color-hover:rgba(151, 207, 255, 1);
  --primary-color-inactive:rgba(192, 226, 255, 1);
  --compact-nav-spacer: rgba(255, 255, 255, 0.3);
  --compact-nav-color: rgba(255, 255, 255, 1);
  --padding-bottom: 1.25em;
}
